.active-line {
    font-size: 2.5rem;
    font-weight: bold;
    color: hsl(0deg 85% 55%);
}

.inactive-line {
    margin-top: 1rem;
}

.lrc {
    --lyrics-margin: 2rem;
    --min-height: calc(20vh - var(--lyrics-margin));
    --max-height: calc(100vh - (100vw / 16 * 9) - var(--lyrics-margin));

    overflow: hidden;
    text-align: center;
    line-height: 2rem;
    margin-top: var(--lyrics-margin);
    height: max(var(--min-height), var(--max-height));
}
